

















































































































import Vue from 'vue'
import axios from 'axios'
import VueAxios from 'vue-axios'
  export default Vue.extend ({
    name: 'ReenviarEncuesta',
    data() : any {
      return {
        mostrar: false,
        disabled:true,
        servicio:null,
        serviciosalud:[],
        establecimiento:null,
        establecimientos:[],
        unidad:null,
        unidades:[],
        encuesta:null,
        encuestas: [],
        datos:[],
        estado_envio:null,
        checked:[],
        seen: false
      }
    },
    computed:{
      selectAll: {
        get() {
          if (this.datos && this.datos.length > 0) { 
            let allChecked: boolean = false;

            for (const dato of this.datos) {
              if (!this.checked.includes(dato.envio_id)) {
                this.allChecked = true
              }
              else{
                this.allChecked = false
              }
              if(!allChecked) break;
            }
            return allChecked;
          }
          return false;
        },
        set(value) {
          const checked = [] as any;
          if (value) {
            this.datos.forEach((dato : any) => {
              checked.push(dato.envio_id);
            });
          }
          else {
            this.checked = []
          }
         console.log(checked)
          this.checked = checked;
        }
      },
    },
    methods: {
      getServicioSalud(): any {
        axios.get('http://192.168.60.105:3007/ugcc-encuestas/obtener-servicios-salud/')
        .then( response => { 
          (this as any).serviciosalud = response.data.data
          })
        .catch(function (error) {
            console.log(error + "No se pueden encontrar servicios de salud relacionados");
        }) 
      },
      getEstablecimiento: async function() {
        try {
          const res = await axios.post('http://192.168.60.105:3007/ugcc-encuestas/obtener-establecimientos/'+(this as any).servicio)
          .then(res => (this as any).establecimientos = res.data.data);
          console.log(res); 
        } catch (err) {
            alert("No se pueden encontrar establecimiento de salud relacionados");
        };
      },
      getUnidad: async function() {
        try {
          const res = await  axios.post('http://192.168.60.105:3007/ugcc-encuestas/obtener-unidades/'+(this as any).establecimiento)
          .then(res => (this as any).unidades = res.data.data);
          console.log(res); 
        } catch (err) {
            alert("No se pueden encontrar unidades relacionadas");
        };
      },
      getTipoEncuesta: async function() {
        try {
          const res = await  axios.get('http://192.168.60.105:3007/ugcc-encuestas/obtener-tipos-encuestas')
          .then(res => (this as any).encuestas = res.data.data);
          console.log(res); 
        } catch (err) {
            alert("No se pueden encontrar tipo de encuestas relacionadas");
        };
      },
      mostrarEncuesta(){ 
        axios({
        method: "post",
        url:"http://192.168.60.105:3007/ugcc-encuestas/obtener-encuestas-reenvio",
        data:{
            establecimiento_id: (this as any).establecimiento,
            unidad_id: (this as any).unidad,
            encuesta_id: (this as any).encuesta,

            estado_envio: (this as any).estado_envio
          }
        })
        .then( response => { this.datos = response.data.data, this.mostrar == true } )
        .catch(function (error) {
            console.log(error);
        }) 
      },
      async reenviarEncuesta(e : any) {
        let arr = [];
        for (const id of this.checked) {
          arr.push( { envio_id: id });
        }
        this.seen = true
        axios({
        method: "post",
        url:'http://192.168.60.105:3007/ugcc-encuestas/reenviar-encuestas',
        data: 
          arr
        })
        .then(data => console.log(data))
        .catch(error => console.error('Error:', error))
        .then(response => console.log('Success:', response))
      },
      onReset(event: any) {
        event.preventDefault()
        // Reset our form values
        this.unidades = []
        this.encuestas = []
        this.datos = []
     
        // Trick to reset/clear native browser form validation state
        /* this.show = false
        this.$nextTick(() => {
          this.show = true
        }) */
      }
    },
    created(){
      (this as any).mostrarEncuesta()
    },
     mounted() {
      (this as any).getServicioSalud()
    }
  })
